.section {
  margin-bottom: 10px;
}

.sectionHeader {
  display: flex;
  align-items: center;
  font-weight: bold;
  padding: 8px 5px;
  cursor: pointer;
  user-select: none;

  svg {
    margin-right: 5px;
  }

  &:hover {
    background-color: #e0e0e0;
    border-radius: 4px;
  }
}
